@import 'colors.scss';
@import 'mixins.scss';
@import 'breakpoints.scss';

.logo {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-left: 2rem;

    &__img {
        width: 5rem;
        @include mq(smartphone) {
            width: 100%;
        }
    }

    &__text {
        font-family: 'Poppins', sans-serif;
        font-weight: bold;
        color: $main-font-color;
        font-size: 2.5rem;
        @include mq(smartphone) {
            font-size: 3.5rem;
        }
        @include mq(tablet) {
            font-size: 4rem;
        }
    }
}