@mixin mq($breakpoint) {
    $size: map-get($breakpoints, $breakpoint);
    @if($size) {
        @media #{$size} {
            @content;
        }
    } @else {
        @error '"#{$breakpoint}" - Size not found';
    }
}

@mixin  wrapper {
    max-width: 136.6rem;
}

@mixin  table-wrapper {
    max-width: 100rem;
}