@import-normalize; //"scss.lint.unknownAtRules": "ignore"
@import 'colors.scss';

html {
    box-sizing: border-box;
    scroll-behavior: smooth;
    font-size: 62.5%;
  }

  *, *::before, *::after {
    box-sizing: inherit;
  }

  body {
    background: $body-bg-color;
    margin: 0;
    padding: 0;
    font-family: 'Poppins', sans-serif;
  }

    /****** Elad Shechter's RESET *******/
/*** box sizing border-box for all elements ***/
*,
*::before,
*::after {
    box-sizing:border-box;
}

a {
    text-decoration:none;
    color:inherit;
    cursor:pointer;
}

button {
    background-color:transparent;
    color:inherit;
    border-width:0;
    padding:0;
    cursor:pointer;
}

input::-moz-focus-inner {
    border:0;
    padding:0;
    margin:0;
}

ul {
    margin:0;
    padding:0;
    list-style:none;
}

h1, h2, h3, h4, h5, h6 {
    margin:0;
    font-size:inherit;
    font-weight:inherit;
}

p {
    margin:0;
}

img {
    width: 100%;
    height: auto;
}